import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { GettingStartedComponent } from './child-components/getting-started/getting-started.component';
import { DirectoryOverviewComponent } from './child-components/directory-overview/directory-overview.component';
import { MainPluginsOverviewComponent } from './child-components/main-plugins-overview/main-plugins-overview.component';
import { MainComponentsComponent } from './child-components/main-components/main-components.component';
import { DevelopingFeaturesComponent } from './child-components/developing-features/developing-features.component';


const routes: Routes = [
  { path: 'getting-started', component: GettingStartedComponent },
  { path: 'directory-overview', component: DirectoryOverviewComponent },
  { path: 'main-plugins-overview', component: MainPluginsOverviewComponent },
  { path: 'main-components', component: MainComponentsComponent },
  { path: 'developing-features', component: DevelopingFeaturesComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AngularRoutingModule { }